* {
    --textsize: 16px;
    --textcolor: #101010;
    --bgcolor: #f0f0f0;
}
body{max-width:650px;
    margin:40px auto;
    padding:0 10px;
    font: var(--textsize)/1.35 system-ui, sans-serif;
    color: var(--textcolor);
    background: var(--bgcolor);
}
h1,h2,h3{line-height:1.2}
@media (prefers-color-scheme: dark){body{color:#ccc;background:black}a:link{color:#5bf}a:visited{color:#ccf}}

.leaflet-container {
  width: 100%;
  aspect-ratio: 16/9;
}

.card {
  max-width: 100%;
  margin: 10px 0px;
  padding-inline: 10px;
  border: 1px solid gainsboro;
  border-radius: 20px;
  background: #f3f3f3;
}

.dropzone {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-width: 2;
  border-radius: 2;
  border-color: #eeeeee;
  border-style: dashed;
  background-color: $fafafa;
  color: #bdbdbd;
  outline: none;
  transition: border .24s ease-in-out
}

.dropzone-hover {
    border-color: #2196f3
}

.logout-button {
    margin-left: 20px;
    margin-top: 5px;
}

.trip-adder {
    margin-top: 30px;
}

.not-found {
    text-align: center;
}

.map-div {
    height: 540px;
    width: 960px;
}

.map {
    height: 100%;
    minHeight: 100%;
    width: 100%;
    minWidth: 100%;
}

.login {
    float: left;
}

.signup {
    float: right;
}

.container {
    display: flex;
}
