* {
  --textsize: 16px;
  --textcolor: #101010;
  --bgcolor: #f0f0f0;
}

body {
  max-width: 650px;
  font: var(--textsize) / 1.35 system-ui, sans-serif;
  color: var(--textcolor);
  background: var(--bgcolor);
  margin: 40px auto;
  padding: 0 10px;
}

h1, h2, h3 {
