}

.card {
  max-width: 100%;
  background: #f3f3f3;
  border: 1px solid #dcdcdc;
  border-radius: 20px;
  margin: 10px 0;
  padding-inline: 10px;
}

.dropzone {
  background-color: $ fafafa;
  color: #bdbdbd;
  border: 2px dashed #eee;
  border-radius: 2px;
  outline: none;
  flex-direction: column;
  flex: 1;
  align-items: center;
  padding: 20px;
  transition: border .24s ease-in-out;
  display: flex;
}

.dropzone-hover {
  border-color: #2196f3;
}

.logout-button {
  margin-top: 5px;
  margin-left: 20px;
}

.trip-adder {
  margin-top: 30px;
}

.not-found {
  text-align: center;
}

.map-div {
  height: 540px;
  width: 960px;
}

.map {
  height: 100%;
  minHeight: 100%;
  width: 100%;
